import { Router } from '@angular/router';
import { FormGroup, Validators, FormControl } from '@angular/forms';
import { ChangeDetectionStrategy, ChangeDetectorRef, Component, Injector, OnInit } from '@angular/core';
import { ActiveModalRef, RxSelectOption } from '@bmc-ux/adapt-angular';
import { RxModalClass } from '@helix/platform/ui-kit';
import {
  RxRecordDefinitionCacheService,
  RxRecordDefinitionService,
  RX_RECORD_DEFINITION
} from '@helix/platform/record/api';
import { RxDefinitionNameService, RxBundleCacheService, RX_APPLICATION } from '@helix/platform/shared/api';
import { finalize, shareReplay, startWith, switchMap, takeUntil } from 'rxjs/operators';
import { combineLatest, of, ReplaySubject } from 'rxjs';
import {
  IExpressionEditorPropertyChange,
  RxDefinitionPickerScope,
  RxDefinitionPickerType,
  RxExpressionEditorService
} from '@helix/platform/shared/components';
import { TranslateService } from '@ngx-translate/core';
import { JoinCriteriaExpressionConfigurator } from './join-criteria-expression-configurator';
import { RxRecordDesignerService } from '@helix/platform/record/designer';

@Component({
  selector: 'rx-create-join-record',
  templateUrl: './create-join-record.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class CreateJoinRecordComponent extends RxModalClass implements OnInit {
  private destroyed$ = new ReplaySubject<boolean>(1);

  private expressionFormControlLabel = this.translateService.instant(
    'com.bmc.arsys.rx.innovation-studio.create-join-record-modal.join-criteria.on-statement.label'
  );

  private expressionFormControlTooltip = this.translateService.instant(
    'com.bmc.arsys.rx.innovation-studio.create-join-record-modal.join-criteria.on-statement.tooltip'
  );

  primaryRecordDefinitionPickerOptions = {
    label: this.translateService.instant(
      'com.bmc.arsys.rx.innovation-studio.create-join-record-modal.primary-record.label'
    ),
    definitionType: RxDefinitionPickerType.Record,
    required: true
  };

  secondaryRecordDefinitionPickerOptions = {
    label: this.translateService.instant(
      'com.bmc.arsys.rx.innovation-studio.create-join-record-modal.secondary-record.label'
    ),
    definitionType: RxDefinitionPickerType.Record,
    required: true
  };

  joinTypes: RxSelectOption[] = [RX_RECORD_DEFINITION.joinTypes.inner, RX_RECORD_DEFINITION.joinTypes.outer];

  private primaryRecordDefinitionNameFormControl = new FormControl('', Validators.required);
  private secondaryRecordDefinitionNameFormControl = new FormControl('', Validators.required);

  private primaryRecordDefinition$ = this.primaryRecordDefinitionNameFormControl.valueChanges.pipe(
    startWith(null),
    switchMap((primaryRecordDefinitionName) =>
      primaryRecordDefinitionName
        ? this.rxRecordDefinitionCacheService.getRecordDefinition(primaryRecordDefinitionName)
        : of(null)
    ),
    shareReplay(1)
  );

  private secondaryRecordDefinition$ = this.secondaryRecordDefinitionNameFormControl.valueChanges.pipe(
    startWith(null),
    switchMap((secondaryRecordDefinitionName) =>
      secondaryRecordDefinitionName
        ? this.rxRecordDefinitionCacheService.getRecordDefinition(secondaryRecordDefinitionName)
        : of(null)
    ),
    shareReplay(1)
  );

  createJoinRecordForm = new FormGroup({
    name: new FormControl('', Validators.required),
    primaryRecordDefinitionName: this.primaryRecordDefinitionNameFormControl,
    secondaryRecordDefinitionName: this.secondaryRecordDefinitionNameFormControl,
    joinType: new FormControl([], Validators.required),
    joinCriteria: new FormControl('')
  });

  private expressionConfigurator = new JoinCriteriaExpressionConfigurator(
    this.primaryRecordDefinition$,
    this.secondaryRecordDefinition$,
    this.injector
  );

  expressionFormControlOptions = {
    label: this.expressionFormControlLabel,
    tooltip: {
      iconName: 'question_circle_o',
      content: this.expressionFormControlTooltip
    },
    dataDictionary$: this.expressionConfigurator.getDataDictionary(),
    operators: this.expressionConfigurator.getOperators()
  };

  constructor(
    protected injector: Injector,
    private activeModalRef: ActiveModalRef,
    private rxRecordDefinitionService: RxRecordDefinitionService,
    private rxDefinitionNameService: RxDefinitionNameService,
    private rxBundleCache: RxBundleCacheService,
    private router: Router,
    private translateService: TranslateService,
    private rxRecordDefinitionCacheService: RxRecordDefinitionCacheService,
    private rxExpressionEditorService: RxExpressionEditorService,
    private rxRecordDesignerService: RxRecordDesignerService,
    private changeDetector: ChangeDetectorRef
  ) {
    super(activeModalRef, injector);
  }

  ngOnInit(): void {
    super.ngOnInit();

    combineLatest([this.primaryRecordDefinition$, this.secondaryRecordDefinition$])
      .pipe(takeUntil(this.destroyed$))
      .subscribe(() => {
        this.createJoinRecordForm.controls.joinCriteria.reset();
      });
  }

  optionFormatter(selectOption: RxSelectOption): string {
    return selectOption.displayName;
  }

  openEditor(): void {
    this.rxExpressionEditorService
      .openEditor({
        property: {
          value: this.createJoinRecordForm.controls.joinCriteria.value,
          path: 'joinCriteria',
          label: this.translateService.instant(
            'com.bmc.arsys.rx.innovation-studio.create-join-record-modal.join-criteria.on-statement.label'
          )
        },
        expressionConfigurator: this.expressionConfigurator,
        legend: [
          {
            label: this.translateService.instant('com.bmc.arsys.rx.client.expression-editor.legend.field.label'),
            icon: 'd-icon-field_text'
          }
        ]
      })
      .pipe(takeUntil(this.destroyed$))
      .subscribe((expression: IExpressionEditorPropertyChange) => {
        this.createJoinRecordForm.controls.joinCriteria.setValue(expression.value);
        this.changeDetector.markForCheck();
      });
  }

  createRecord(): void {
    const createJoinRecordFormValue = this.createJoinRecordForm.value;

    const recordDefinitionName = this.rxDefinitionNameService.getDefinitionName(
      this.rxBundleCache.bundleId,
      createJoinRecordFormValue.name
    );

    const joinRecordDefinition = {
      allowFieldsOverlay: false,
      allowIndexesOverlay: false,
      allowNonAdminToDeleteRecordInstances: false,
      allowOtherPropertiesOverlay: false,
      allowPermissionsOverlay: false,
      name: recordDefinitionName,
      primaryRecordDefinitionName: createJoinRecordFormValue.primaryRecordDefinitionName,
      secondaryRecordDefinitionName: createJoinRecordFormValue.secondaryRecordDefinitionName,
      joinType: createJoinRecordFormValue.joinType[0].value,
      joinCriteria: this.rxRecordDesignerService.getJoinCriteriaArExpression(
        createJoinRecordFormValue.joinCriteria,
        createJoinRecordFormValue.primaryRecordDefinitionName,
        createJoinRecordFormValue.secondaryRecordDefinitionName
      ),
      resourceType: RX_RECORD_DEFINITION.recordDefinitionTypes.join.recordDefinitionType,
      fieldDefinitions: [
        {
          resourceType: RX_RECORD_DEFINITION.resourceTypes.character,
          name: 'Display ID',
          id: 1,
          fieldOption: RX_RECORD_DEFINITION.fieldOptions.system
        },
        {
          resourceType: RX_RECORD_DEFINITION.resourceTypes.character,
          name: 'ID',
          id: 379,
          fieldOption: RX_RECORD_DEFINITION.fieldOptions.system
        }
      ]
    };

    this.allowDismiss = false;

    this.rxRecordDefinitionService
      .create(joinRecordDefinition)
      .pipe(
        finalize(() => {
          this.allowDismiss = true;
          this.changeDetector.markForCheck();
        })
      )
      .subscribe(() => {
        this.router.navigate([RX_APPLICATION.innovationStudioBundleId, 'record', 'edit2', recordDefinitionName]);
      });
  }

  cancel() {
    this.dismissDialog();
  }
}
